.nav {
  display: none;
}
@media screen and (min-width: 768px) {
  .nav {
    display: flex;
    gap: 10px;
  }
  .link {
    text-decoration: none;
    position: relative;
    color: var(--priority-text-color);
    border-radius: 3px;
    padding: 3px;
    &:global.active {
      color: var(--accent-color);
    }
    &:global.active::after {
      position: absolute;
      left: 0;
      bottom: -19px;

      display: block;
      width: 100%;
      height: 4px;
      content: '';

      background-color: var(--accent-color);
      box-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
      border-radius: 2px;
    }
  }
}
